<template>
  <div class="cycle-report-page">
    <!-- Left Pane -->
    <div class="left-pane">
      <div class="cycle-report">
        <div class="center" v-if="loading">
          <p-progress-spinner />
        </div>

        <div v-else class="knobs-container">
          <div v-if="gData.HCGgt10_perc">
            <p-knob
              v-model="gData.HCGgt10_perc"
              :readonly="true"
              :size="120"
              :value-color="'#FFA726'"
            />
            <span class="knob-label">HCG > 10%</span>
          </div>

          <div v-if="gData.GS_perc">
            <p-knob
              v-model="gData.GS_perc"
              :readonly="true"
              :size="120"
              :value-color="'#42A5F5'"
            />
            <span class="knob-label">GS %</span>
          </div>

          <div v-if="gData.HB_perc">
            <p-knob
              v-model="gData.HB_perc"
              :readonly="true"
              :size="120"
              :value-color="'#66BB6A'"
            />
            <span class="knob-label">HB %</span>
          </div>

          <div v-if="gData.Loss_perc">
            <p-knob
              v-model="gData.Loss_perc"
              :readonly="true"
              :size="120"
              :value-color="'#EF5350'"
            />
            <span class="knob-label">Loss %</span>
          </div>
        </div>

        <div class="center chart-container">
          <Bar
            :chart-data="leftChartData"
            :options="chartOptions"
            :height="400"
            :width="600"
          />
        </div>
      </div>
    </div>

    <!-- Right Pane -->
    <div class="right-pane">
      <div class="cycle-report">
        <div class="center" v-if="loading">
          <p-progress-spinner />
        </div>

        <div v-else class="knobs-container">
          <div v-if="gData.FR_perc">
            <p-knob
              v-model="gData.FR_perc"
              :readonly="true"
              :size="120"
              :value-color="'#FFA726'"
            />
            <span class="knob-label">Fert Rate</span>
          </div>

          <div v-if="gData.BR_perc">
            <p-knob
              v-model="gData.BR_perc"
              :readonly="true"
              :size="120"
              :value-color="'#42A5F5'"
            />
            <span class="knob-label">Blast Rate</span>
          </div>
        </div>

        <div class="center chart-container">
          <Bar
            :chart-data="rightChartData"
            :options="chartOptions"
            :height="400"
            :width="300"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useCycleReportTable } from "@/composable/cycle-report";
import { computed, onMounted, onUnmounted } from "vue";
import Knob from "primevue/knob";
import { emitter } from "@/event-bus";
import { Bar } from "vue-chartjs";
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale
} from "chart.js";

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale);

export default {
  components: {
    "p-knob": Knob,
    Bar
  },
  setup() {
    const { store, getCycleKPIC, loading } = useCycleReportTable();
    const gData = computed(() => store.state.cycleReport.gData);

    // Chart data for the left pane
    const leftChartData = computed(() => ({
      labels: ["HCG > 10%", "GS%", "HB%", "Loss%"],
      datasets: [
        {
          label: "KPI-C",
          data: [
            gData.value.HCGgt10_perc,
            gData.value.GS_perc,
            gData.value.HB_perc,
            gData.value.Loss_perc
          ],
          backgroundColor: ["#FFA726", "#42A5F5", "#66BB6A", "#EF5350"],
          borderColor: ["#FB8C00", "#1E88E5", "#43A047", "#E53935"],
          borderWidth: 1
        }
      ]
    }));

    // Chart data for the right pane
    const rightChartData = computed(() => ({
      labels: ["Fert Rate", "Blast Rate"],
      datasets: [
        {
          label: "KPI-C",
          data: [gData.value.FR_perc, gData.value.BR_perc],
          backgroundColor: ["#FFA726", "#42A5F5"],
          borderColor: ["#FB8C00", "#1E88E5"],
          borderWidth: 1
        }
      ]
    }));

    const chartOptions = {
      responsive: true,
      scales: {
        y: {
          beginAtZero: true,
          max: 100
        }
      }
    };

    onMounted(() => {
      getCycleKPIC();
      emitter.on("cycle-report-table:get-cycle-kpic", getCycleKPIC);
    });

    onUnmounted(() => {
      emitter.off("cycle-report-table:get-cycle-kpic", getCycleKPIC);
    });

    return {
      gData,
      leftChartData,
      rightChartData,
      loading,
      chartOptions
    };
  }
};
</script>

<style scoped>
.cycle-report-page {
  display: flex;
  justify-content: space-between;
}

.left-pane,
.right-pane {
  flex: 1;
  padding: 20px;
}

.cycle-report {
  width: 100%; 
  max-width: 800px; 
}

.chart-container {
  height: 400px;
}
.knobs-container {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
